import { Day } from "../types";
import { loadImage } from "../helper";

import qrCode from './qr-code.png';

import sprites from './sprites.jpg';
import spritesData from './sprites_data.json';


import preval from 'preval.macro';


export default class Day13 extends Day {
  public static __hash = "fDzejmzXQGYVITUknsduQzs";
  public static __name = "Day13";

  public static __qrCode = qrCode;

  
  public static __content = async () => preval`
    const marked = require('marked');
    const twemoji = require('twemoji');
    const path = require('path');
    const fs = require('fs');

    const markdownPath = path.join(__dirname, 'content.md');
    const markdown = fs.readFileSync(markdownPath, 'utf-8')

    const _html = marked(markdown);
    const html = twemoji.parse(_html);

    module.exports = html;
  `;
  

  private static framesSpritePromise = loadImage(sprites);
  private static framesSprite: HTMLImageElement;
  private static framesCanvas = document.createElement('canvas');
  private static framesContext = Day13.framesCanvas.getContext('2d');

  private static currentFrame: number = 0;

  private static fps = 1000 / 20;
  private static last = 0;

  public static async init() {
    if (Day13.framesSprite === undefined) {
      await Day13.loadFrames();
    }
  }

  private static async loadFrames() {
    Day13.framesSprite = await Day13.framesSpritePromise;
  }

  public static getFrame(): HTMLCanvasElement {
    if (Day13.framesContext === null) throw new Error("Day13.framesContext === null");
    
    const now = Date.now();
    const elapsed = now - Day13.last;

    if (elapsed >= Day13.fps) {
      Day13.last = now;
      Day13.currentFrame = (Day13.currentFrame + 1) % spritesData.length;

      const { x, y, width, height } = spritesData[Day13.currentFrame];

      Day13.framesCanvas.width = width;
      Day13.framesCanvas.height = height;

      Day13.framesContext.drawImage(
        Day13.framesSprite,
        x, y, width, height,
        0, 0, width, height,
      )
    }

    return Day13.framesCanvas;
  }
}
